import React from "react";
import { useDispatch } from "react-redux";
import { toast } from "react-toastify";
import { useTranslation } from "react-i18next";
import { removeFromCartState, changeQty } from "../../actions/cart";
function InputChangerCountAddCart({
	initValue,
	onChange,
	variationId,
	addToCartHandler,
	max_count,
	token,
	min,
	disable = true
}) {
	const { t } = useTranslation();
	const dispatch = useDispatch();
	function removeFromCart(variationId, count) {
		if (count > 0) {
			dispatch(changeQty(variationId, count, token));
			toast.error(t("remove_cart_sucsessfuly"), {
				position: "top-left"
			});
		} else {
			dispatch(removeFromCartState(variationId));
		}
	}
	return (
		<div className="form-control input-changer-count d-flex p-3 justify-content-between">
			<span
				style={{ cursor: "pointer" }}
				onClick={() => {
					onChange(
						parseInt(initValue) - 1 > min
							? parseInt(initValue) - 1
							: min,
						variationId,
						max_count
					);
					removeFromCart(variationId, initValue - 1);
				}}
			>
				-
			</span>
			<input
				style={{
					textAlign: "center",
					border: "none",
					width: "100%",
					outline: "none"
				}}
				disabled={disable}
				onChange={e =>
					onChange(
						parseInt(e.target.value) > min
							? parseInt(e.target.value)
							: min,
						variationId,
						max_count
					)
				}
				value={!initValue || initValue < 1 ? 1 : initValue}
				required
			/>
			<span
				style={{ cursor: "pointer" }}
				onClick={() => {
					onChange(parseInt(initValue) + 1, variationId, max_count);
					addToCartHandler();
				}}
			>
				+
			</span>
		</div>
	);
}

export default InputChangerCountAddCart;
