import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { getBrandAttribute, getImageUrl } from "../../../utils/oxMethods";
import { connect, useDispatch, useSelector } from "react-redux";
import addToCartCheck from "./addToCartCheck";
import { getVariation } from "../../../api/products";
import { useTranslation } from "react-i18next";
import { setChoosenProduct } from "../../../actions/choosenProducts";
import ProductImages from "./details/ProductImages";
import market from "../../../config/market";
import { formatterCurrency } from "../../../utils/string";
import Loader from "../loaders/loader";

function ProductCardTwo(props) {
	const { cart } = useSelector((state) => state.cartlist);
	const { product, settings, onAddToCart, haveSupplier = false } = props;
	const { token } = useSelector((state) => state.user.tokenData);
	const dispatch = useDispatch();
	const [loading, setLoading] = useState(false);
	const [setSupplier] = useState();
	const [currentImage, setCurrentImage] = useState(0);

	const images = product?.variation?.images;

	const { t } = useTranslation();

	const brandAttribute = getBrandAttribute(product, settings);

	const { productList } = useSelector((state) => state.choosenProducts);

	const haveChoosen = productList.find(
		(p) => p.stock.id === product.stock.id
	);

	useEffect(() => {
		if (haveSupplier && props.suppliers) {
			const supplier = props.suppliers.find(
				(s) => s.id === product.stock.supplier
			);
			setSupplier(supplier);
		}
	}, [haveSupplier, props.suppliers]);

	const addToCartHandler = () => {
		setLoading(true);
		getVariation(product.variation.id)
			.then((dataProduct) => {
				if (dataProduct.variations.length === 1) {
					addToCartCheck(
						dataProduct,
						1,
						token,
						setLoading,
						dispatch,
						cart
					);
				} else {
					onAddToCart(dataProduct, 1);
					setLoading(false);
				}
			})
			.catch((err) => {
				console.error(err);
			});
	};

	// if (images[currentImage]) {
	// 	var myImg = document.querySelector("#sky");
	// 	var realWidth = myImg.naturalWidth;
	// 	var realHeight = myImg.naturalHeight;
	// 	alert(
	// 		"Original width=" +
	// 			realWidth +
	// 			", " +
	// 			"Original height=" +
	// 			realHeight
	// 	);
	// }

	return product ? (
		<div className="card-seven">
			<div className="product-header">
				<div className="image-blur">
					<img
						src={getImageUrl(images[currentImage])}
						alt="background"
					/>
				</div>
				<div className="active-image">
					<button
						className={`favourites-button ${
							haveChoosen ? `choosen` : ``
						}`}
						onClick={() => {
							dispatch(setChoosenProduct(product));
						}}
					>
						<img
							src={
								haveChoosen
									? market.config.icons.card_favourites_active
									: market.config.icons.card_favourites
							}
							alt="choosen"
						/>
					</button>
					{images.length > 1 ? (
						<div className="change-image">
							<span className="left">
								<i
									onClick={() =>
										setCurrentImage(
											images[currentImage - 1]
												? currentImage - 1
												: currentImage
										)
									}
									className="icon-angle-left"
								></i>
							</span>
							<span className="right">
								<i
									onClick={() =>
										setCurrentImage(
											images[currentImage + 1]
												? currentImage + 1
												: currentImage
										)
									}
									className="icon-angle-right"
								></i>
							</span>
						</div>
					) : null}
					<Link to={`/product/${product.variation.id}`}>
						<ProductImages
							currentImage={currentImage}
							setCurrentImage={setCurrentImage}
							images={images}
						/>
					</Link>
				</div>
			</div>
			<div className="body-product">
				<span className="price-new">
					{formatterCurrency(product.newPrice)}
				</span>
				<div className="price-monthly">
					{formatterCurrency((product.newPrice * 1.29) / 12)}&nbsp;
					<span>{window.innerWidth < 992 ? null : "|"} В месяц</span>
				</div>
				<div className="title-brand">
					<h6 className="title-product">
						<Link to={"/product/" + product.variation.id}>
							{product.variation.name}
						</Link>
					</h6>
					{brandAttribute ? (
						<span className="name-supplier">{brandAttribute}</span>
					) : null}
				</div>
				<div className="action-product">
					{loading ? (
						<button className="cart-btn">
							<Loader />
						</button>
					) : (
						<button className="cart-btn" onClick={addToCartHandler}>
							<img src={market.config.icons.cart} alt="cart" />
							<span>{t("add-cart")}</span>
						</button>
					)}
				</div>
			</div>
		</div>
	) : null;
}

export default connect((state) => ({ settings: state.marketData.settings }))(
	ProductCardTwo
);
