import React from "react";
import market from "../../../config/market";
import ProductCardOne from "./ProductCardOne";
import ProductCardTwo from "./ProductCardTwo";
import ProductCardThree from "./ProductCardThree";
import ProductCardFour from "./ProductCardFour";
import ProductCardFive from "./ProductCardFive";
import ProductCardSix from "./ProductCardSix";
import ProductCardSeven from "./ProductCardSeven";

export default function ProductCard(props) {
	switch (market.config.productCard) {
		case 1:
			return <ProductCardOne {...props} />;
		case 2:
			return <ProductCardTwo {...props} />;
		case 3:
			return <ProductCardThree {...props} />;
		case 4:
			return <ProductCardFour {...props} />;
		case 5:
			return <ProductCardFive {...props} />;
		case 6:
			return <ProductCardSix {...props} />;
		case 7:
			return <ProductCardSeven {...props} />;
		default:
			return <ProductCardOne {...props} />;
	}
}
