import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { getBrandAttribute } from "../../../utils/oxMethods";
import { formatterCurrency } from "../../../utils/string";
import { connect, useDispatch, useSelector } from "react-redux";
import addToCartCheck from "./addToCartCheck";
import InputChangerCountAddCart from "../../../components/common/InputChangerCountAddCart";
import { getVariation } from "../../../api/products";
import { useTranslation } from "react-i18next";
import { toast } from "react-toastify";
import { setChoosenProduct } from "../../../actions/choosenProducts";
import ProductImages from "./details/ProductImages";
import market from "../../../config/market";

function ProductCardTwo(props) {
	const { cart } = useSelector((state) => state.cartlist);
	const { product, settings, onAddToCart, haveSupplier = false } = props;
	const { token } = useSelector((state) => state.user.tokenData);
	const dispatch = useDispatch();
	function setLoading() {}
	const [setSupplier] = useState();
	const [currentImage, setCurrentImage] = useState(0);

	const images = product?.variation?.images;

	const { t } = useTranslation();

	const brandAttribute = getBrandAttribute(product, settings);

	const { productList } = useSelector((state) => state.choosenProducts);

	const haveCart = cart.find((p) => p.data.stock.id === product.stock.id);

	const haveChoosen = productList.find(
		(p) => p.stock.id === product.stock.id
	);

	useEffect(() => {
		if (haveSupplier && props.suppliers) {
			const supplier = props.suppliers.find(
				(s) => s.id === product.stock.supplier
			);
			setSupplier(supplier);
		}
	}, [haveSupplier, props.suppliers]);

	const addToCartHandler = () => {
		setLoading(true);
		getVariation(product.variation.id)
			.then((dataProduct) => {
				if (dataProduct.variations.length === 1) {
					addToCartCheck(
						dataProduct,
						1,
						token,
						setLoading,
						dispatch,
						cart
					);
				} else {
					onAddToCart(dataProduct, 1);
					setLoading(false);
				}
			})
			.catch((err) => {
				console.error(err);
			});
	};
	const max_count = parseInt(product.variation.count);
	function onChange(qty) {
		if (parseInt(qty) > max_count) {
			toast.error(t("amount-balance"), {
				position: "bottom-left",
			});
		}
	}

	return product ? (
		<div className="card-two">
			<div className="product-header">
				<button
					className={`favourites-button ${
						haveChoosen ? `choosen` : ``
					}`}
					onClick={() => {
						dispatch(setChoosenProduct(product));
					}}
				>
					<svg
						width="27"
						height="28"
						viewBox="0 0 27 28"
						fill="none"
						xmlns="http://www.w3.org/2000/svg"
					>
						<path
							d="M13.5594 6.34495L12.9375 6.85023L12.3155 6.34495C9.84658 4.33919 6.28733 4.53937 4.05162 6.80974C1.78232 9.11421 1.61561 12.7981 3.7859 15.1989C6.9221 18.6682 11.1636 22.9641 12.9375 22.9641C14.7113 22.9641 18.9528 18.6682 22.089 15.1989C24.2593 12.7981 24.0926 9.11421 21.8233 6.80974C19.5876 4.53937 16.0284 4.33919 13.5594 6.34495Z"
							fill="#04A0E6"
						/>
					</svg>
				</button>
				{images.length > 1 ? (
					<div className="change-image">
						<span className="left">
							<i
								onClick={() =>
									setCurrentImage(
										images[currentImage - 1]
											? currentImage - 1
											: currentImage
									)
								}
								className="icon-angle-left"
							></i>
						</span>
						<span className="right">
							<i
								onClick={() =>
									setCurrentImage(
										images[currentImage + 1]
											? currentImage + 1
											: currentImage
									)
								}
								className="icon-angle-right"
							></i>
						</span>
					</div>
				) : null}
				<Link to={`/product/${product.variation.id}`}>
					<ProductImages
						currentImage={currentImage}
						setCurrentImage={setCurrentImage}
						images={images}
					/>
				</Link>
			</div>
			<div className="body-product">
				<h6 className="title-product">
					<Link to={"/product/" + product.variation.id}>
						{product.variation.name}
					</Link>
				</h6>
				{brandAttribute ? (
					<div className="brand">
						<img
							src="/assets/images/icons/baby_center/brand-icon.svg"
							alt={product.variation.name}
						/>
						<span className="name-supplier">{brandAttribute}</span>
					</div>
				) : null}
				<div className="price-with-cart">
					{product.discount > 0 ? (
						<div className="price-product price-with-discount">
							<span className="price-old">
								<del>{formatterCurrency(product.oldPrice)}</del>
							</span>
							<span className="price-new">
								{formatterCurrency(product.newPrice)}
							</span>
						</div>
					) : (
						<div className="price-product">
							<span className="price-new">
								{formatterCurrency(product.newPrice)}
							</span>
						</div>
					)}

					<div className="action-product">
						{haveCart ? (
							<div className="product-details-quantity">
								<InputChangerCountAddCart
									initValue={haveCart.count}
									addToCartHandler={addToCartHandler}
									onChange={(value) =>
										onChange(value, product.variation.id)
									}
									variationId={product.variation.id}
									token={token}
								/>
							</div>
						) : (
							<button
								className="btn-cart"
								onClick={() => {
									addToCartHandler();
								}}
							>
								<img
									src={market.config.icons.card_cart}
									alt="cart"
								/>
							</button>
						)}
					</div>
				</div>
			</div>
		</div>
	) : null;
}

export default connect((state) => ({ settings: state.marketData.settings }))(
	ProductCardTwo
);
