import React, { useState } from "react";
import { Link } from "react-router-dom";
import { openModal } from "../../../actions/modal";
import { getBrandAttribute } from "../../../utils/oxMethods";
import { formatterCurrency } from "../../../utils/string";
import Loader from "../../../components/features/loaders/loader";
import { connect, useDispatch, useSelector } from "react-redux";
import { getVariation } from "../../../api/products";
import { setChoosenProduct } from "../../../actions/choosenProducts";
import { useTranslation } from "react-i18next";
import addToCartCheck from "./addToCartCheck";
import MediaImage from "./MediaImage";

function ProductCardFour(props) {
	const { product, onAddToCart, settings } = props;
	const { token } = useSelector((state) => state.user.tokenData);
	const { cart } = useSelector((state) => state.cartlist);
	const [loading, setLoading] = useState(false);
	const dispatch = useDispatch();
	const brandAttribute = getBrandAttribute(product, settings);
	const { t } = useTranslation();

	const addToCartHandler = () => {
		product.data = { ...product };
		setLoading(true);
		getVariation(product.variation.id)
			.then((dataProduct) => {
				if (dataProduct.variations.length === 1) {
					addToCartCheck(
						dataProduct,
						1,
						token,
						setLoading,
						dispatch,
						cart
					);
				} else {
					onAddToCart(dataProduct, 1);
					setLoading(false);
				}
			})
			.catch((err) => {
				console.error(err);
			});
	};

	const quickViewHandler = () => {
		dispatch(openModal("qv", { productId: product.variation.id }));
	};

	const { productList } = useSelector((state) => state.choosenProducts);
	const haveChoosen = productList.find(
		(p) => p.stock.id === product.stock.id
	);

	let image = product?.variation?.images[0];

	return product ? (
		<div className="card-four">
			<div className="product-header">
				<div
					className="discount"
					style={
						product.discount > 0
							? { display: "flex" }
							: { display: "none" }
					}
				>
					<span className="text">
						-
						{(
							100 -
							(product.newPrice * 100) / product.oldPrice
						).toFixed(0)}
						%
					</span>
				</div>
				<Link to={`/product/${product.variation.id}`}>
					<MediaImage
						image={image}
						largeSize={"500x_"}
						mediumSize={"350x_"}
						smallSize={"150x_"}
						alt={product.name}
					/>
				</Link>
				<button
					className="choosen-button"
					onClick={() => {
						dispatch(setChoosenProduct(product));
					}}
				>
					{haveChoosen ? (
						<svg
							viewBox="0 0 24 21"
							fill="none"
							xmlns="http://www.w3.org/2000/svg"
						>
							<path
								d="M10.2796 1.30413L9.71428 1.78133L9.14897 1.30413C6.90486 -0.590203 3.66972 -0.401141 1.63759 1.7431C-0.425067 3.91954 -0.57659 7.39879 1.39607 9.66622C4.24668 12.9428 8.10196 17 9.71428 17C11.3266 17 15.1819 12.9428 18.0325 9.66622C20.0052 7.39879 19.8536 3.91954 17.791 1.74309C15.7589 -0.401141 12.5237 -0.590203 10.2796 1.30413Z"
								fill="#FF6060"
							/>
						</svg>
					) : (
						<svg
							viewBox="0 0 24 21"
							fill="none"
							xmlns="http://www.w3.org/2000/svg"
						>
							<path
								d="M10.7143 2.78133L10.2305 3.35444L10.7143 3.76282L11.1981 3.35444L10.7143 2.78133ZM11.2796 2.30413L11.7634 2.87724V2.87724L11.2796 2.30413ZM19.0325 10.6662L19.5983 11.1585L19.0325 10.6662ZM2.39607 10.6662L1.83024 11.1585H1.83024L2.39607 10.6662ZM10.149 2.30413L10.6328 1.73102V1.73102L10.149 2.30413ZM18.791 2.74309L19.3354 2.22719V2.22719L18.791 2.74309ZM11.1981 3.35444L11.7634 2.87724L10.7958 1.73102L10.2305 2.20822L11.1981 3.35444ZM18.4667 10.174C17.0471 11.8057 15.3903 13.6165 13.8966 15.0142C13.1483 15.7144 12.458 16.2947 11.8707 16.6953C11.5773 16.8954 11.3243 17.0405 11.1143 17.1334C10.8971 17.2295 10.7696 17.25 10.7143 17.25V18.75C11.0621 18.75 11.4078 18.6438 11.7211 18.5052C12.0417 18.3634 12.3767 18.1658 12.716 17.9345C13.3941 17.4719 14.1481 16.8331 14.9215 16.1095C16.4712 14.6594 18.1673 12.8033 19.5983 11.1585L18.4667 10.174ZM10.7143 17.25C10.659 17.25 10.5315 17.2295 10.3142 17.1334C10.1043 17.0405 9.85132 16.8954 9.55785 16.6953C8.97059 16.2947 8.28027 15.7144 7.53197 15.0142C6.03829 13.6165 4.38151 11.8057 2.9619 10.1739L1.83024 11.1585C3.26124 12.8033 4.95741 14.6594 6.5071 16.1095C7.28049 16.8331 8.03446 17.4719 8.71262 17.9345C9.05186 18.1658 9.38686 18.3634 9.70749 18.5052C10.0208 18.6438 10.3665 18.75 10.7143 18.75V17.25ZM9.66519 2.87724L10.2305 3.35444L11.1981 2.20822L10.6328 1.73102L9.66519 2.87724ZM3.18196 3.259C4.94332 1.40047 7.72774 1.24177 9.66519 2.87724L10.6328 1.73102C8.08198 -0.422178 4.39611 -0.202753 2.09322 2.22719L3.18196 3.259ZM2.9619 10.1739C1.25439 8.21128 1.3727 5.16807 3.18196 3.259L2.09322 2.22719C-0.222836 4.67101 -0.407566 8.5863 1.83024 11.1585L2.9619 10.1739ZM18.2466 3.259C20.0559 5.16807 20.1742 8.21128 18.4667 10.174L19.5983 11.1585C21.8361 8.5863 21.6514 4.67101 19.3354 2.22719L18.2466 3.259ZM11.7634 2.87724C13.7008 1.24177 16.4853 1.40047 18.2466 3.259L19.3354 2.22719C17.0325 -0.202753 13.3466 -0.422179 10.7958 1.73102L11.7634 2.87724Z"
								fill="#293644"
							/>
						</svg>
					)}
				</button>
				<button
					className="quickView-button"
					title="Quick view"
					onClick={quickViewHandler}
				>
					<span>{t("quick-view")}</span>
				</button>
				<div className="action-product">
					{loading ? (
						<button className="btn-cart">
							<Loader />
						</button>
					) : (
						<button className="btn-cart" onClick={addToCartHandler}>
							<span className="verticalDiv"></span>
							<span className="horizontalDiv"></span>
						</button>
					)}
				</div>
			</div>

			<div className="body-product">
				{0 < product.discount ? (
					<div className="price-product old">
						<span className="price-old">
							<del>{formatterCurrency(product.oldPrice)}</del>
						</span>
						<span className="price-new">
							{formatterCurrency(product.newPrice)}
						</span>
					</div>
				) : (
					<div className="price-product">
						<span className="price-new price-new-two">
							{formatterCurrency(product.newPrice)}
						</span>
					</div>
				)}

				<h6 className="title-product">
					<Link to={"/product/" + product.variation.id}>
						{brandAttribute ? (
							<span className="name-supplier">
								{brandAttribute}
							</span>
						) : null}
						/{product.variation.name}
					</Link>
				</h6>
			</div>
		</div>
	) : null;
}

export default connect((state) => ({ settings: state.marketData.settings }))(
	ProductCardFour
);
