import React from "react";
import { getImageUrl } from "../../../../utils/oxMethods";

export default function ProductImages({
	currentImage,
	setCurrentImage,
	images
}) {
	return (
		<>
			{images.length > 1 && (
				<div className="image_pagination_wrapper">
					{images.slice(0, 5).map((_, index) => {
						return (
							<span
								key={index}
								onMouseMove={() => setCurrentImage(index)}
								className={`other_image ${
									currentImage === index ? "active" : ""
								}`}
							></span>
						);
					})}
				</div>
			)}
			{images.length > 0 ? (
				<img
					src={getImageUrl(images[currentImage], "800x_")}
					alt="productimage"
				/>
			) : (
				<img
					src="https://telegra.ph/file/3a01e53662cea1a425220.png"
					alt="product"
				/>
			)}
		</>
	);
}
