import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { getBrandAttribute } from "../../../utils/oxMethods";
import { formatterCurrency } from "../../../utils/string";
import { connect, useDispatch, useSelector } from "react-redux";
import addToCartCheck from "./addToCartCheck";
import Loader from "../../../components/features/loaders/loader";
import MediaImage from "./MediaImage";
import { getVariation } from "../../../api/products";
import { setChoosenProduct } from "../../../actions/choosenProducts";
import { useTranslation } from "react-i18next";
import market from "../../../config/market";
import InputChangerCountAddCart from "../../common/InputChangerCountAddCart";
import { toast } from "react-toastify";
import { setCompareProducts } from "../../../actions/comparisonProducts";
import { dt } from "../../../i18n";

function ProductCardSix(props) {
	const { t } = useTranslation();

	const { cart } = useSelector((state) => state.cartlist);

	const { product, settings, onAddToCart, haveSupplier = false } = props;

	const { token } = useSelector((state) => state.user.tokenData);

	const dispatch = useDispatch();

	const [loading, setLoading] = useState(false);

	const [setSupplier] = useState();

	const brandAttribute = getBrandAttribute(product, settings);

	const { productList } = useSelector((state) => state.choosenProducts);
	const { productListCompare } = useSelector(
		(state) => state.comparisonProducts
	);

	const haveChoosen = productList.find(
		(p) => p.stock.id === product.stock.id
	);

	const haveCompare = productListCompare.find(
		(p) => p?.stock?.id === product?.stock?.id
	);

	const haveCart = cart.find((p) => p.data.stock.id === product.stock.id);

	useEffect(() => {
		if (haveSupplier && props.suppliers) {
			const supplier = props.suppliers.find(
				(s) => s.id === product.stock.supplier
			);
			setSupplier(supplier);
		}
	}, [haveSupplier, props.suppliers]);

	const addToCartHandler = () => {
		setLoading(true);
		getVariation(product.variation.id)
			.then((dataProduct) => {
				if (dataProduct.variations.length === 1) {
					addToCartCheck(
						dataProduct,
						1,
						token,
						setLoading,
						dispatch,
						cart
					);
				} else {
					onAddToCart(dataProduct, 1);
					setLoading(false);
				}
			})
			.catch((err) => {
				console.error(err);
			});
	};
	const max_count = parseInt(product?.variation.count);

	function onChange(qty) {
		if (parseInt(qty) > max_count) {
			toast.error(t("amount-balance"), {
				position: "bottom-left",
			});
		}
	}
	const images = product?.variation?.images[0];
	// product.discount = 50;
	return product ? (
		<div className="card-two card-six">
			<div className="product-header">
				{product.discount > 0 ? (
					<div className="discount">
						<span className="text">
							-
							{(
								100 -
								(product.newPrice * 100) / product.oldPrice
							).toFixed(0)}
							%
						</span>
					</div>
				) : null}
				<div className="header-buttons">
					{market.config.compare ? (
						<button
							className={`compare-button ${
								haveCompare ? `have-compare` : ``
							}`}
							onClick={() => {
								dispatch(setCompareProducts(product));
							}}
						>
							<img
								src={market.config.icons.compare}
								alt="compare"
							/>
						</button>
					) : null}
					<button
						className={`favourites-button ${
							haveChoosen ? `have-choosen` : ``
						}`}
						onClick={() => {
							dispatch(setChoosenProduct(product));
						}}
					>
						<img
							src={market.config.icons.favourites}
							alt="favourites"
						/>
					</button>
				</div>

				<Link to={`/product/${product.variation.id}`} className="w-100">
					<MediaImage
						image={images}
						largeSize={"300x_"}
						mediumSize={"150x_"}
						smallSize={"150x_"}
						alt={product.name}
					/>
				</Link>
			</div>
			<div className="body-product">
				<div className="price-title">
					<div className="price-product">
						<span
							className={`price-new ${
								product.discount ? `discount` : ``
							}`}
						>
							{formatterCurrency(product.newPrice)}
						</span>
						{product.discount ? (
							<span className="price-old">
								<del>{formatterCurrency(product.oldPrice)}</del>
							</span>
						) : null}
					</div>
					<h6 className="title-product">
						<Link to={"/product/" + product.variation.id}>
							{dt(product.variation.productName)}
						</Link>
					</h6>

					<div className="brand">
						{brandAttribute ? (
							<span className="name-supplier">
								{brandAttribute}
							</span>
						) : null}
					</div>
				</div>
				<div className="sale-with-cart">
					<div className="action-product">
						{loading ? (
							<button className="cart-btn">
								<Loader />
							</button>
						) : haveCart ? (
							<div className="product-details-quantity">
								<InputChangerCountAddCart
									initValue={haveCart.count}
									addToCartHandler={addToCartHandler}
									onChange={(value) =>
										onChange(value, product.variation.id)
									}
									variationId={product.variation.id}
									token={token}
								/>
							</div>
						) : (
							<button
								className={`${
									haveCart ? `cart-btn added` : `cart-btn`
								}`}
								onClick={addToCartHandler}
							>
								{haveCart ? (
									<>
										<img
											src={market.config.icons.added_cart}
											alt="cart"
										/>
										{t("added-cart")}
									</>
								) : (
									<>
										<img
											src={market.config.icons.cart}
											alt="cart"
										/>
										{t("add-cart")}
									</>
								)}
							</button>
						)}
					</div>
					{/* <div className="sale">
						<p>в рассрочку от</p>
						<div className="sale-price">
							<div className="d-flex align-items-center">
								<img
									src="/assets/images/icons/davrmarket/sale-left.svg"
									alt="sale"
								/>
								<span>20 000 UZS</span>
								<img
									src="/assets/images/icons/davrmarket/sale-right.svg"
									alt="sale"
								/>
							</div>
							<p className="monthly">в месяц</p>
						</div>
					</div> */}
				</div>
			</div>
		</div>
	) : null;
}

export default connect((state) => ({ settings: state.marketData.settings }))(
	ProductCardSix
);
