import React, { useState } from "react";
import { Link } from "react-router-dom";
import { openModal } from "../../../actions/modal";
import { getBrandAttribute } from "../../../utils/oxMethods";
import { formatterCurrency } from "../../../utils/string";
import { connect, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import { getVariation } from "../../../api/products";
import Loader from "../loaders/loader";
import ProductImages from "./details/ProductImages";
import { setChoosenProduct } from "../../../actions/choosenProducts";
import addToCartCheck from "./addToCartCheck";

function ProductCardThree(props) {
	const { product, onAddToCart, dispatch, settings } = props;
	const { token } = useSelector((state) => state.user.tokenData);
	const { cart } = useSelector((state) => state.cartlist);
	const [loading, setLoading] = useState(false);
	const { t } = useTranslation();
	const [currentImage, setCurrentImage] = useState(0);
	const { productList } = useSelector((state) => state.choosenProducts);
	const haveChoosen = productList.find(
		(p) => p.stock.id === product.stock.id
	);
	const images = product?.variation?.images;

	const productPercent = 100 - (product.newPrice * 100) / product.oldPrice;
	const brandAttribute = getBrandAttribute(product, settings);

	const addToCartHandler = () => {
		setLoading(true);
		getVariation(product.variation.id)
			.then((dataProduct) => {
				if (dataProduct.variations.length === 1) {
					addToCartCheck(
						dataProduct,
						1,
						token,
						setLoading,
						dispatch,
						cart
					);
				} else {
					onAddToCart(dataProduct, 1);
					setLoading(false);
				}
			})
			.catch((err) => {
				console.error(err);
			});
	};

	const quickViewHandler = () => {
		dispatch(openModal("qv", { productId: product.variation.id }));
	};

	function toTop() {
		window.scroll({
			top: 0,
		});
	}

	return product ? (
		<div className="card-three">
			<div className="product-header">
				{productPercent && (
					<div className="discount">{productPercent.toFixed(0)}%</div>
				)}
				<div className="change-image">
					<span className="left">
						<i
							onClick={() =>
								setCurrentImage(
									images[currentImage - 1]
										? currentImage - 1
										: currentImage
								)
							}
							className="icon-angle-left"
						></i>
					</span>
					<span className="right">
						<i
							onClick={() =>
								setCurrentImage(
									images[currentImage + 1]
										? currentImage + 1
										: currentImage
								)
							}
							className="icon-angle-right"
						></i>
					</span>
				</div>

				<Link to={`/product/${product.variation.id}`} onClick={toTop}>
					<ProductImages
						currentImage={currentImage}
						setCurrentImage={setCurrentImage}
						images={images}
					/>
				</Link>

				<button
					className="btn-product-icon btn-quickview"
					title="Quick view"
					onClick={quickViewHandler}
				>
					<span>{t("quick-view")}</span>
				</button>
				<button
					className="btn-heart"
					onClick={() => dispatch(setChoosenProduct(product))}
				>
					<i
						className={`icon icon-heart btn-product-icon ${
							haveChoosen && "active"
						}`}
					></i>
				</button>
			</div>
			<div className="body-product">
				{loading ? (
					<button className="btn-product cart-btn">
						<Loader />
					</button>
				) : (
					<button className="cart-btn" onClick={addToCartHandler}>
						<span>{t("add-cart")}</span>
					</button>
				)}
				<h6 className="title-product">
					<Link to={"/product/" + product.variation.id}>
						{brandAttribute ? (
							<span className="supplier-name">
								{brandAttribute} /
							</span>
						) : null}
						{product.variation.name}
					</Link>
				</h6>

				<div className="price-product">
					<span className="price-new">
						{formatterCurrency(product.newPrice)}
					</span>
					{0 < product.discount && (
						<span className="price-old">
							<del>{formatterCurrency(product.oldPrice)}</del>
						</span>
					)}
				</div>
			</div>
		</div>
	) : null;
}

export default connect((state) => ({ settings: state.marketData.settings }))(
	ProductCardThree
);
