import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { openModal } from "../../../actions/modal";
import { getBrandAttribute } from "../../../utils/oxMethods";
import { formatterCurrency } from "../../../utils/string";
import { connect, useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import Loader from "../loaders/loader";
import addToCartCheck from "./addToCartCheck";
import { getVariation } from "../../../api/products";
import { setChoosenProduct } from "../../../actions/choosenProducts";
import MediaImage from "./MediaImage";

function ProductCardOne(props) {
	const { cart } = useSelector((state) => state.cartlist);
	const { product, onAddToCart, settings, haveSupplier = false } = props;
	const { token } = useSelector((state) => state.user.tokenData);
	const { t } = useTranslation();
	const dispatch = useDispatch();
	const [loading, setLoading] = useState(false);
	const [supplier, setSupplier] = useState();

	const brandAttribute = getBrandAttribute(product, settings);

	useEffect(() => {
		if (haveSupplier && props.suppliers) {
			const supplier = props.suppliers.find(
				(s) => s.id === product.stock.supplier
			);
			setSupplier(supplier);
		}
	}, [haveSupplier, props.suppliers]);

	const addToCartHandler = () => {
		product.data = { ...product };
		setLoading(true);
		getVariation(product.variation.id)
			.then((dataProduct) => {
				if (dataProduct.variations.length === 1) {
					addToCartCheck(
						dataProduct,
						1,
						token,
						setLoading,
						dispatch,
						cart
					);
				} else {
					onAddToCart(dataProduct, 1);
					setLoading(false);
				}
			})
			.catch((err) => {
				console.error(err);
			});
	};

	const quickViewHandler = () => {
		dispatch(openModal("qv", { productId: product.variation.id }));
	};

	function toTop() {
		window.scroll({
			top: 0,
		});
	}
	const { productList } = useSelector((state) => state.choosenProducts);
	const haveChoosen = productList.find(
		(p) => p.stock.id === product.stock.id
	);
	let image = product.variation.images[0];

	return product ? (
		<div className="card-one">
			<div className="header-witch-body">
				<div className="product-header">
					<Link
						to={`/product/${product.variation.id}`}
						onClick={toTop}
					>
						<MediaImage
							image={image}
							largeSize={"500x_"}
							mediumSize={"500x_"}
							smallSize={"500x_"}
							alt={product.name}
						/>
					</Link>

					<button
						className="btn-product-icon btn-quickview"
						title="Quick view"
						onClick={quickViewHandler}
					>
						<span>{t("quick-view")}</span>
					</button>
					<button
						className="btn-heart"
						onClick={() => dispatch(setChoosenProduct(product))}
						style={{
							color: haveChoosen ? "red" : "",
						}}
					>
						{haveChoosen ? (
							<img
								src="/assets/images/vibo/choosen-heart.svg"
								alt="heart"
							/>
						) : (
							<img
								src="/assets/images/vibo/not-choosen-heart.svg"
								alt="heart"
							/>
						)}
					</button>
					{loading ? (
						<button className="btn-product cart-btn">
							<Loader />
						</button>
					) : (
						<button className="cart-btn" onClick={addToCartHandler}>
							<span>{t("add-cart")}</span>
						</button>
					)}
				</div>
				<div className="body-product">
					<h6 className="title-product">
						<Link to={"/product/" + product.variation.id}>
							{brandAttribute ? (
								<span className="d-none">{brandAttribute}</span>
							) : null}
							{brandAttribute ? (
								<span className="supplier-name">
									{brandAttribute} /
								</span>
							) : null}
							{product.variation.name}
						</Link>
					</h6>

					<div className="price-product">
						<span className="price-new">
							{formatterCurrency(product.newPrice)}
						</span>
					</div>
				</div>
			</div>
			{supplier ? (
				<Link to={`/supplier/${supplier.id}`}>
					<div className="product-supplier">
						<div className="supplier-img">
							<MediaImage
								image={supplier.logo}
								largeSize={"150x_"}
								mediumSize={"150x_"}
								smallSize={"150x_"}
								alt={supplier.name}
							/>
						</div>
						<p>{supplier.name}</p>
					</div>
				</Link>
			) : null}
		</div>
	) : null;
}

export default connect((state) => ({ settings: state.marketData.settings }))(
	ProductCardOne
);
